.form-signin {
  max-width: 450px;
  margin: 2em auto;
  background: #f5f5f5;
  padding: 0.5em 2.5em 2.3em;
  box-shadow: 0 2px 0 #d0cccc,0 4px 3px #f1f1f1;
  border-radius: 2px;
  .form-signin-heading {
    margin: 1em 0;
    color: #b7b7b7;
  }
  label {
    font-family: cursive;
    color: #6e777b;
    font-weight: 300;
  }
}



